<template>
	<view class="normal-login-container">
		<view class="logo-content align-center justify-center flex">
			<image style="width: 300rpx;height: 200rpx;" :src="globalConfig.appInfo.logo" mode="widthFix">
			</image>
		</view>
		<view class="login-form-content">
			<view class="input-item flex align-center">
				<view class="iconfont icon-user icon"></view>
				<input v-model="loginForm.username" class="input" type="text" placeholder="请输入账号" maxlength="30" />
			</view>
			<view class="input-item flex align-center">
				<view class="iconfont icon-password icon"></view>
				<input v-model="loginForm.password" type="password" class="input" placeholder="请输入密码" maxlength="20" />
			</view>
			<view class="input-item flex align-center" v-if="cacheswitch">
				<view class="iconfont icon-code icon"></view>
				<input v-model="loginForm.code" type="number" class="input" placeholder="请输入验证码" maxlength="4" />
				<image :src="codeUrl" @click="getCode" class="login-code-img"></image>
			</view>
			<view class="action-btn">
				<button @click="handleLogin" class="login-btn cu-btn block bg-blue lg round">登录</button>
				<!-- <button style="margin-top: 20rpx;" @click="handleRegister" class="login-btn cu-btn block bg-gray lg round">注册</button> -->
			</view>
		</view>

		<!-- <view class="xieyi text-center">
			<text class="text-grey1">登录即代表同意</text>
			<text @click="handleUserAgrement" class="text-blue">《用户协议》</text>
			<text @click="handlePrivacy" class="text-blue">《隐私协议》</text>
		</view> -->
	</view>
</template>

<script>
	import {
		getCodeImg
	} from '@/api/login'
	import {
		queryUserCourse
	} from '@/api/user/search.js'
	export default {
		data() {
			return {
				codeUrl: "",
				cacheswitch: true,
				globalConfig: getApp().globalData.config,
				loginForm: {
					username: "",
					password: "",
					code: "",
					uuid: ''
				},
				userInfo: {
					username: "",
					userId: "",
					avatar: "",
					courseId: []
				}
			}
		},
		created() {
			this.getCode()
		},
		methods: {
			// 隐私协议
			handlePrivacy() {
				let site = this.globalConfig.appInfo.agreements[0]
				this.$tab.navigateTo(`/pages/common/webview/index?title=${site.title}&url=${site.url}`)
			},
			// 用户协议
			handleUserAgrement() {
				let site = this.globalConfig.appInfo.agreements[1]
				this.$tab.navigateTo(`/pages/common/webview/index?title=${site.title}&url=${site.url}`)
			},
			// 获取图形验证码
			getCode() {
				getCodeImg().then(res => {
					this.cacheswitch = res.data.cacheswitch === undefined ? true : res.data.cacheswitch
					if (this.cacheswitch) {
						this.codeUrl = 'data:image/gif;base64,' + res.data.img
						this.loginForm.uuid = res.data.uuid
					}
				})
			},
			// 登录方法
			async handleLogin() {
				if (this.loginForm.username === "") {
					this.$modal.msgError("请输入您的账号")
				} else if (this.loginForm.password === "") {
					this.$modal.msgError("请输入您的密码")
				} else if (this.loginForm.code === "" && this.cacheswitch) {
					this.$modal.msgError("请输入验证码")
				} else {
					this.$modal.loading("登录中，请耐心等待...")
					this.pwdLogin()
				}
			},
			async handleRegister(){
				this.$tab.navigateTo('/pages/me/info/index')
			},
			// 密码登录
			async pwdLogin() {
				this.$store.dispatch('Login', this.loginForm).then(res => {
					this.userInfo.username = res.data.username
					this.userInfo.userId = res.data.userId
					this.userInfo.avatar = res.data.avatar
					//查用户已经拥有的课程
					queryUserCourse(res.data.userId).then(res => {
						this.userInfo.courseId = res.data.map(userCourseInfo=>{return userCourseInfo.courseId})
						this.$modal.closeLoading()
						this.loginSuccess()
					})

				}).catch(() => {
					this.$modal.closeLoading()
					// if (this.cacheswitch) {
						this.getCode()
					// }
				})
			},
			// 登录成功后，处理函数
			loginSuccess() {
				// 设置用户信息
				this.$store.dispatch('SetUserInfo', this.userInfo)
				this.$tab.reLaunch('/pages/index')
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #ffffff;
	}

	.normal-login-container {
		width: 100%;

		.logo-content {
			width: 100%;
			font-size: 21px;
			text-align: center;
			padding-top: 15%;

			image {
				border-radius: 4px;
			}

			.title {
				margin-left: 10px;
			}
		}

		.login-form-content {
			text-align: center;
			margin: 20px auto;
			margin-top: 15%;
			width: 80%;

			.input-item {
				margin: 20px auto;
				background-color: #f5f6f7;
				height: 45px;
				border-radius: 20px;

				.icon {
					font-size: 38rpx;
					margin-left: 10px;
					color: #999;
				}

				.input {
					width: 100%;
					font-size: 14px;
					line-height: 20px;
					text-align: left;
					padding-left: 15px;
				}

			}

			.login-btn {
				margin-top: 40px;
				height: 45px;
			}

			.xieyi {
				color: #333;
				margin-top: 20px;
			}
		}

		.easyinput {
			width: 100%;
		}
	}

	.login-code-img {
		height: 45px;
	}
</style>
